<template>
  <div class="moves">
    <!-- 二级路由 -->
    <div class="title-cut">
      <van-tabs  @click="onClick(activeName)"
       animated  line-width="26px" v-model="activeName"  title-active-color="#ee0a24" :border="false">
        <van-tab title="正在热映" name='1'>
          <div class="moves-list">
            <ul class="uls" >
              <li @click="viewmoves({name: 'detailsv', params: {id: item.id}})" ref="moviebox"  class="lis"  v-for="(item, index) in  this.result1" :key="index" :id="item.id">
                <div class="showing-left">
                  <img
                   :src="item.images.small" alt v-lazy="{src:item.images.small,loading: 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1576141964201&di=b65f41ae2c0efca1c382dec9882b63ed&imgtype=0&src=http%3A%2F%2Fhbimg.huabanimg.com%2F23907369b8a233f4b426d739875621c6b0fc248a1d28c-8V4M0p_fw658'}"
                  />
                </div>
                <div class="showing-right">
                  <div class="showing-list">
                    <div class="showing-title">
                      <h2>{{item.title}}</h2>
                      <div class="title-logo">
                        <span>3D IMAX</span>
                      </div>
                    </div>
                    <div class="showing-text">
                      <span>{{item.original_title}}</span>
                    </div>
                    <div class="showing-name">
                      <div class="showing-name1">
                        主演:
                        <span>{{item.avatarsInfo}}</span>
                      </div>
                      <div class="showing-name2">
                        导演:
                        <span>{{item.directc}}</span>
                      </div>
                    </div>
                    <div class="showing-btn">
                      <div class="btn-grade">
                        <p>淘票票评分</p>
                        <span>{{item.rating.average}}分</span>
                      </div>
                      <div class="btn-button">购票</div>
                    </div>
                  </div>
                </div>
              </li>
            </ul>
          </div>
        </van-tab>
        <!-- --- -->
        <van-tab title="即将上映" name='2'>
