<script>
export default {
  name: "moves",
  data() {
    return {
      activeName:name,
      result1:'',
      index1:'',
      index2:0,
      newmoves:'',
      maxW:0,
      tomorrow:'',
      afattomorrow:'',
      showPrise:true,
      showRentPrise:true,
      //转换后的时间
      Ntomr:'',
      Aftomr:'',
    //  保存明天上映的电影
      nexttimer:[],
    // 保存后天上映的电影
      aftertimer:[],
    };
  },
  methods: {
      geimoves(){
        let self=this;
         //开启加载提示
      this.$toast.loading({
        duration: 0,
        message: '加载中...'
      });
      this.axios({
        method: "GET",
        url: "https://douban.uieee.com/v2/movie/in_theaters"
      })
        .then(result => {
          //合并演员导演
          result.data.subjects.forEach(v => {
            v.avatarsInfo = "";
            v.index1 = 1;
            v.casts.forEach(item => {
              v.avatarsInfo += item.name + "、";
            });
            this.index1 += v.index1;
            v.avatarsInfo = v.avatarsInfo.slice(0, -1);
            v.directc = "";
            v.directors.forEach(item => {
              v.directc += item.name + "、";
              v.directc = v.directc.slice(0, -1);
            });
           
            
          });
          this.result1 = result.data.subjects;
          this.$toast.clear();
        })
       
        
      
     },
     //切换电影详情
     viewmoves(o){
        this.$router.push(o);
     },
    //  获取明后天上映的电影
      timermoves(){
         //获取过去两天的时间
       //明天
        let day1 = new Date();
       day1.setTime(day1.getTime()+24*60*60*1000);
       let s1 = day1.getFullYear()+"-" + (day1.getMonth()+1) + "-" + day1.getDate();   
      let tomorrow=s1
      this.tomorrow=tomorrow;
     
      //后天
       let day2 = new Date();
      day2.setTime(day2.getTime()+2*24*60*60*1000);
      let s2 = day2.getFullYear()+"-" + (day2.getMonth()+1) + "-" + day2.getDate(); 
      let afattomorrow=s2;
      this.afattomorrow=afattomorrow;
     
      //将时间转换为XX月XX日
      let  newday=new Date();
      var moth=newday.getMonth();
      var date1=newday.getDate()+1;
      var date2=newday.getDate()+2;
      if (moth < 10) moth = "0" + moth;
       if (date1 < 10) date1 = "0" + date1;
       if (date2 < 10) date2 = "0" + date2;
      this.Ntomr=moth+'月'+date1+'日';
      this.Aftomr=moth+'月'+date2+'日';
      this.axios({
        method:'GET',
        url:'https://douban.uieee.com/v2/movie/coming_soon'
      })
      .then(result =>{
        result.data.subjects.forEach(v => {
        v.Newstar="";
        v.casts.forEach(item=>{
             v.Newstar += item.name + "、";
               v.Newstar =  v.Newstar.slice(0, -1);  
        });
        v.newstor="";
        v.directors.forEach(item=>{
            v.newstor += item.name + "、";
            v.newstor = v.newstor.slice(0, -1);  
        });
          // 判断电影上映日期是否是明天和后天                
            if(v.mainland_pubdate == this.tomorrow){
            // 保存明天上映的电影
              this.nexttimer.push(v);          
            }
            if(v.mainland_pubdate == this.afattomorrow){
              //保存后天上映的电影
               this.aftertimer.push(v);            
            }
              
        });
      });
      
      },

    //请求新片电影数据
      getnewmoves(){
      //开启加载提示
      this.$toast.loading({
        duration: 0,
        message: '加载中...'
      });
        //请求新片电影数据
      this.axios({
        methods: "GET",
        url: "https://douban.uieee.com/v2/movie/new_movies?apikey=0df993c66c0c636e29ecbb5344252a4a"
      }).then(reche => {
       
        reche.data.subjects.forEach(v => {
          this.index2+=1;
        });
        this.newmoves = reche.data.subjects;
        
       setTimeout(()=>{
          //设置ul宽度
         this.$nextTick(()=>{
           let kistW= this.$refs.kist[0].clientWidth+10;
           //动态设置ul的宽度
           this.maxW=kistW*reche.data.subjects.length;
          })
       },100)
          this.$toast.clear();
      })
     
      },
       onClick(name){
        // if(name==2){
        //    this. getnewmoves();
          
        // }
        // return
       }
       
  },
  created() {
     this.geimoves();
     this.timermoves();
     this.getnewmoves();
    
  }
};
</script>